@import url(//netdna.bootstrapcdn.com/bootstrap/3.0.0/css/bootstrap-glyphicons.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: left;
}

.jumbotron {
  margin-bottom: 0px;
  padding: 0px;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color:#d8a799;
  height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: white;
  position:fixed;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.fixedContainer {
  position: absolute;
  z-index: 500;

}

.nav {
  background-color: #B5DFCA;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15);
}

.nav-logo {
  height: 60px;
  width: 60px;
}

.nav-logo:hover {
  cursor: pointer;
}

.nav .nav-content {
  max-width: 900px;
  padding: 0rem 3rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Archivo', sans-serif;
  font-weight: 500;
 }
p, div {
  font-family: 'Open Sans', sans-serif;
 font-weight: 300;

}

body { 
  background: url(data:image/png;base64,) repeat 0 0;
}

a {
  color: black !important;
  text-decoration: underline;
}

a:hover {
  text-decoration: underline;
}

a:active {
  color: white;
}

ol {
  list-style: none;
  counter-reset: item;
}
ol li {
  counter-increment: item;
  margin-bottom: 5px;
  font-size:10px;
}
ol li:before {
  margin-right: 10px;
  content: counter(item);
  background: #FFDDED;
  border-radius: 100%;
  color: black;
  width: 1.2em;
  text-align: center;
  display: inline-block;
}

.imgCompany {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px;
    max-width: 40px;
    max-height:40px;
  }
  
.imgExtra {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 5px;
    width: 80px;
  }
  
.yelpwebsitepic {
    width:500px;
    
}

.yelpdropdownpic {
    width:350px;
    
}

.yelpzoominpic {
    width:500px;
    
}
